import Image from "next/image";
import Link from "next/link";
import { notFound } from "next/navigation";
import { getArticleBySlug } from "@/lib/db";
import type { Metadata } from "next";
import ArticleContent from "@/components/ArticleContent";

interface PageProps {
  params: Promise<{ slug: string }>;
}

export async function generateMetadata({ params }: PageProps): Promise<Metadata> {
  const { slug } = await params;
  const article = await getArticleBySlug(slug);

  if (!article) {
    return {
      title: "Không tìm thấy bài viết",
    };
  }

  return {
    title: `${article.title} - Tech News`,
    description: article.summary,
    openGraph: {
      title: article.title,
      description: article.summary,
      images: [article.cover_image_url],
    },
  };
}

export default async function PostPage({ params }: PageProps) {
  const { slug } = await params;
  const article = await getArticleBySlug(slug);

  if (!article) {
    notFound();
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <Link
            href="/"
            className="text-blue-700 hover:text-blue-900 font-semibold inline-flex items-center gap-2"
          >
            ← Quay lại trang chủ
          </Link>
        </div>
      </header>

      <main className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <article className="bg-white rounded-lg shadow-md overflow-hidden">
          <div className="relative h-96 w-full">
            <Image
              src={article.cover_image_url}
              alt={article.title}
              fill
              className="object-cover"
              priority
            />
          </div>

          <div className="p-8">
            <div className="mb-4 text-sm text-gray-600 font-medium">
              {new Date(article.published_at).toLocaleDateString('vi-VN', {
                year: 'numeric',
                month: 'long',
                day: 'numeric'
              })}
            </div>

            <h1 className="text-4xl font-bold text-gray-900 mb-4 leading-tight">
              {article.title}
            </h1>

            <p className="text-xl text-gray-700 mb-8 italic border-l-4 border-blue-600 pl-4">
              {article.summary}
            </p>

            <div className="prose prose-lg max-w-none">
              {article.content.split('\n').map((paragraph, index) => {
                if (paragraph.startsWith('# ')) {
                  return (
                    <h1 key={index} className="text-3xl font-bold mt-8 mb-4 text-gray-900">
                      {paragraph.replace('# ', '')}
                    </h1>
                  );
                }
                if (paragraph.startsWith('## ')) {
                  return (
                    <h2 key={index} className="text-2xl font-bold mt-6 mb-3 text-gray-900">
                      {paragraph.replace('## ', '')}
                    </h2>
                  );
                }
                if (paragraph.startsWith('**') && paragraph.endsWith('**')) {
                  return (
                    <p key={index} className="font-bold mb-4 text-gray-900">
                      {paragraph.replace(/\*\*/g, '')}
                    </p>
                  );
                }
                if (paragraph.match(/^\d+\./)) {
                  return (
                    <li key={index} className="ml-6 mb-2 text-gray-800">
                      {paragraph.replace(/^\d+\.\s*/, '')}
                    </li>
                  );
                }
                if (paragraph.startsWith('- ')) {
                  return (
                    <li key={index} className="ml-6 mb-2 text-gray-800">
                      {paragraph.replace('- ', '')}
                    </li>
                  );
                }
                if (paragraph.trim() === '') {
                  return <br key={index} />;
                }
                return (
                  <p key={index} className="mb-4 text-gray-800 leading-relaxed text-base">
                    {paragraph.replace(/\*\*(.+?)\*\*/g, '<strong>$1</strong>').split('<strong>').map((part, i) => {
                      if (i === 0) return part;
                      const [bold, rest] = part.split('</strong>');
                      return (
                        <span key={i}>
                          <strong className="text-gray-900">{bold}</strong>
                          {rest}
                        </span>
                      );
                    })}
                  </p>
                );
              })}
            </div>
          </div>
        </article>
      </main>
    </div>
  );
}
